/* Popup Container Styles */

/* Popup Container Styles */

#popup-container {
  z-index: 800;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.0.4);
  animation: popup-open 0.5s ease;
}

/* Popup Styles */

.popup {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 600px;
  height: auto;
  background-color: #9673ff;
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  animation: popup-open 0.5s ease;
}

.popup1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 600px;
  height: auto;
  background-color: #9673ff;
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  animation: popup-open 0.5s ease;
}

@keyframes popup-open {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.popup-container.hide {
  animation: popup-close 0.5s ease;
  animation-fill-mode: forwards;
}

@keyframes popup-close {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.popup h2 {
  margin-top: 0;
}

/* Close Button Styles */

#close-btn {
  position: absolute;
  bottom: 10px;
  right: 10px;
  background-color: transparent;
  border: none;
  font-size: 16px;
  cursor: pointer;
}

